import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, Renderer2, OnDestroy, Output, EventEmitter } from '@angular/core';

import { BaseComponent } from '../../../../shared/globals/base.component';
import { IBooking } from '../../../../services/data/bookings.service';

import * as moment from 'moment';

interface ITimelineBlock {
    /** Block time as ms since UTC epoch */
    value: number;
    /** Whether to show block divider */
    show: boolean;
    /** Hour of the day represented by this time block */
    hour: number;
    /** Number of minutes from the expired length time */
    offset: number;
    /** Whether block is in use or not */
    in_use: boolean;
}

@Component({
    selector: 'explore-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss']
})
export class ExploreTimelineComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {
    /** Amount of time to display on the timeline */
    @Input() public duration = 12 * 60;
    /** Size of each time block */
    @Input() public block_size = 30;
    /** Time offset in minutes which is displayed before the current time marker */
    @Input() public expired_length = 2 * 60;
    /** Events occuring on the timeline */
    @Input() public events: IBooking[] = [];
    /** Emitter for changes to the selected time */
    @Output() public time = new EventEmitter<number>();

    /** List of time blocks to display on the DOM */
    public blocks: ITimelineBlock[] = [];
    /** Active time */
    public selected_time: number;
    /** Offset of the selected time from the current time in minutes */
    public selected_offset = 0;
    /** Offset of the selected time from the current time in days */
    public date_offset = 0;
    /** Modulus 5 for the minutes of the current time  */
    public modulo: number;
    /** Display value of the selected time */
    public display_time: string;
    /** Display value of the selected date */
    public display_date: string;

    /** Bounding box for the root element of the component */
    private cntr_box: ClientRect;
    /** Listener for grab move events */
    private _move_listener: () => void;
    /** Listener for grab end events */
    private _release_listener: () => void;

    @ViewChild('container') public container: ElementRef<HTMLDivElement>;

    constructor(private renderer: Renderer2) {
        super();
    }

    public ngOnInit(): void {
        this.generateTimelineBlocks();
        this.selected_offset = 0;
        this.date_offset = 0;
        const start = moment()
            .add(this.selected_offset, 'm')
            .add(this.date_offset, 'd')
            .startOf('m');
        start.minutes(Math.ceil(start.minutes() / 5) * 5);
        this.display_time = start.format('h:mm a');
        this.display_date = start.isSame(moment(), 'd') ? 'Today' : start.format('DD MMM YYYY');
        this.interval('expired', () => this.generateTimelineBlocks(), 60 * 1000);
    }

    public ngAfterViewInit(): void {
        this.resize();
    }

    public ngOnDestroy(): void {
        this.handleRelease();
    }

    /** Length of the shown timeline in minutes */
    public get length(): number {
        return this.duration + this.block_size;
    }

    /**
     * Generate time blocks to display
     */
    private generateTimelineBlocks(): void {
        let now = moment()
            .add(this.date_offset, 'd')
            .startOf('m');
        if (this.date_offset) {
            now.hour(7).minutes(0);
        }
        now = now.minute(Math.round(now.minute() / 5) * 5);
        const start = moment(now).subtract(this.date_offset ? 30 : this.expired_length || 120, 'm');
        const end = moment(start).add(this.duration || 720, 'm');
        const date = moment(start);
        delete this.blocks;
        this.blocks = [];
        while (date.isBefore(end, 'm')) {
            this.blocks.push({
                value: date.valueOf(),
                show: date.minute() % ((this ? this.block_size : null) || 30) === 0,
                hour: date.minute() === 0 ? (date.hour() % 12 === 0 ? 12 : date.hour() % 12) : null,
                offset: date.diff(now, 'm'),
                in_use: false
            });
            date.add(5, 'm');
        }
        this.updateSelected();
    }

    /**
     * Handle click and touch events on timeline
     * @param event Touch or Click event on timeline
     */
    public selectTime(event: MouseEvent | TouchEvent): void {
        const center = {
            x: event instanceof MouseEvent ? event.clientX : event.touches[0].clientX,
            y: event instanceof MouseEvent ? event.clientY : event.touches[0].clientY
        };
        if (!this.cntr_box) {
            this.cntr_box = this.container.nativeElement.getBoundingClientRect();
        }
        const offset = this.date_offset ? 0 : this.expired_length / this.duration;
        const percent = (center.x - this.cntr_box.left) / this.cntr_box.width - offset;
        const length = Math.ceil((this.duration - (this.date_offset ? 0 : this.expired_length) - 30) / 5) * 5;
        this.selected_offset = Math.min(
            length,
            Math.max(this.date_offset ? 30 : 0, this.duration * percent)
        );
        const now = moment().startOf('m');
        if (this.date_offset) {
            now.hours(7)
                .minutes(0)
                .subtract(30, 'm');
        }
        const start = moment(now)
            .add(this.selected_offset, 'm')
            .add(this.date_offset, 'd');
        start.minutes(Math.ceil(start.minutes() / 5) * 5);
        this.selected_time = start.valueOf();
        this.display_time = start.format('h:mm a');
        this.display_date = start.isSame(moment(), 'd') ? 'Today' : start.format('DD MMM YYYY');
        this.time.emit(start.startOf('m').valueOf());
    }

    /**
     * Updates the selected offset
     * @param offset New offset value
     */
    public set(offset: number): void {
        this.selected_offset = Math.min(
            this.duration - (this.date_offset ? 0 : this.expired_length) - 30,
            Math.max(this.date_offset ? 30 : 0, offset)
        );
        const now = moment().startOf('m');
        if (this.date_offset) {
            now.hours(7)
                .minutes(0)
                .subtract(30, 'm');
        }
        const start = moment(now)
            .add(this.selected_offset, 'm')
            .add(this.date_offset, 'd');
        start.minutes(Math.ceil(start.minutes() / 5) * 5);
        this.selected_time = start.valueOf();
        this.display_time = start.format('h:mm a');
        this.display_date = start.isSame(moment(), 'd') ? 'Today' : start.format('DD MMM YYYY');
        this.time.emit(start.startOf('m').valueOf());
    }

    /**
     * Change the selected date of the timeline
     * @param offset Offset to change the select date by in number of days
     */
    public changeDate(offset: number = 1): void {
        this.clearTimeout('grab');
        this.date_offset += offset;
        let new_offset = this.selected_offset;
        if (this.date_offset === 1 && offset > 0) {
            new_offset = this.expired_length + this.selected_offset;
        } else if (this.date_offset === 0 && offset < 0) {
            new_offset = this.selected_offset - this.expired_length;
        }
        this.set(new_offset);
        this.generateTimelineBlocks();
    }

    /**
     * Update bounding boxes after viewport change
     */
    public resize(): void {
        if (this.container) {
            this.cntr_box = this.container.nativeElement.getBoundingClientRect();
        }
    }

    /**
     * Event callback for mousedown/touchstart events of the selected time elements
     * @param event Thrown event
     */
    public grab(event: MouseEvent | TouchEvent): void {
        this.timeout('grab', () => {
            this.handleRelease();
            if (!this.cntr_box) {
                this.cntr_box = this.container.nativeElement.getBoundingClientRect();
            }
            if (event instanceof MouseEvent) {
                this._move_listener = this.renderer.listen('window', 'mousemove', e => this.handleMove(e));
                this._release_listener = this.renderer.listen('window', 'mouseup', _ => this.handleRelease());
            } else {
                this._move_listener = this.renderer.listen('window', 'touchmove', e => this.handleMove(e));
                this._release_listener = this.renderer.listen('window', 'touchend', _ => this.handleRelease());
            }
        }, 10);
    }

    /**
     * Handle moving after grab event
     * @param event
     */
    private handleMove(event: MouseEvent | TouchEvent): void {
        this.selectTime(event);
    }

    /**
     * Handle end of grab event
     */
    private handleRelease(): void {
        if (this._move_listener) {
            this._move_listener();
            this._move_listener = null;
        }
        if (this._release_listener) {
            this._release_listener();
            this._release_listener = null;
        }
    }

    /**
     * Update the percentage of the day that has expired
     */
    private updateSelected(): void {
        const now = moment().add(this.date_offset, 'd');
        if (this.date_offset) {
            now.hours(7)
                .minutes(0)
                .subtract(30, 'm');
        }
        const diff = moment(this.selected_time).diff(now, 'm');
        if (diff < 0) {
            const start = moment(now).startOf('m');
            this.selected_time = start.valueOf();
            this.display_time = start.format('h:mm a');
            this.display_date = start.isSame(moment(), 'd') ? 'Today' : start.format('DD MMM YYYY');
            start.minutes(Math.ceil(start.minutes() / 5) * 5);
            this.time.emit(start.valueOf());
        }
        this.selected_offset = Math.min(
            this.duration - (this.date_offset ? 0 : this.expired_length) - 30,
            Math.max(this.date_offset ? 30 : 0, diff)
        );
    }
}
